<template>
  <div class="map-wrap">
    <el-amap-search-box
      class="search-box"
      :events="searchEvent"
    ></el-amap-search-box>
    <el-amap
      ref="map"
      :vid="vid"
      :zoom="18"
      :events="events"
      :plugin="plugin"
      :center="center">
      <el-amap-marker
        v-for="(marker, index) in markerData"
        :key="index"
        :position="marker.position"
        :events="marker.events"
        :content="marker.content"
        :vid="index"
      ></el-amap-marker>
    </el-amap>
  </div>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue'
  import VueAMap from "vue-amap";

  VueAMap.initAMapApiLoader({
    key: '7073fee18f7ca5c51c91e15f7d708cae',
    plugin: [
      'AMap.Geolocation'
    ]
  })

  Vue.use(VueAMap)
  export default {
    props: {
      markerData: {
        type: Array,
        default: () => []
      }
    },
    data() {
      const _self = this
      return {
        placeSearch: null,
        searchEvent: {
          init(o) {
            _self.placeSearch = o.placeSearch
          }
        },
        center: [121.59996, 31.197646],
        lng: 0,
        lat: 0,
        vid: new Date().toString(),
        events: {
          init() {
            _self.$emit('getCenter', _self.$refs.map.$$getInstance().getCenter())
          },
          moveend() {
            _self.$emit('getCenter', _self.$refs.map.$$getInstance().getCenter())
          }
        },
        plugin: [{
          pName: 'Geolocation',
          buttonDom: '<div class="map-location-btn"></div>',
          buttonPosition: 'RB',
          events: {
            init(o) {
              // o 
              o.getCurrentPosition((status, result) => {
                if (result && result.position) {
                  _self.lng = result.position.lng;
                  _self.lat = result.position.lat;
                  _self.center = [_self.lng, _self.lat];
                  _self.$emit('getCenter', {lng: _self.lng, lat: _self.lat})
                  _self.$nextTick();
                }
              });
            }
          }
        }]
      }
    },
    created() {

    },
    beforeDestroy() {
      localStorage.clear()
    }
  }
</script>
<style scoped lang="less">
  .map-wrap{
    width: 100%;
    height: 100%;
    .search-box{
      position: absolute;
      z-index: -1 !important;
    }
  }
</style>

